import ImageGallery from "@/components/LightBox";
import { useGalleryQuery } from "@/hooks/queries/useGalleryQuery";
import React from "react";

const GalleriPage = () => {
  const { data, isLoading, isError } = useGalleryQuery();

  return <main>{data && <ImageGallery images={data.map((galleryData)=>galleryData.image)} baseUrl="http://localhost:4444/images/gallery/"/>}</main>;
};

export default GalleriPage;
